@import "variables.css";
@import "font-face-rules.css";

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: black;
}

body {
  font-family: var(--typography-font-family-primary);
  margin: 0;
  overflow: hidden;
}

#scene-container {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.ui-elements {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  width: 200px;
  border: 2px solid #232323;
  background: white;
  padding: 5px;
  font-size: 14px;
  font-family: Helvetica, sans-serif;
}

.control:not(:first-child) {
  margin-top: 10px;
}

.tool-state {
  display: flex;
}

.horizontal-radios .radio-label {
  display: block;
  position: relative;
  font-size: 12px;
}

.horizontal-radios .radio-label input {
  position: absolute;
  left: 5px;
  bottom: 2px;
  font-size: 0;
  z-index: -1;
}

.horizontal-radios .radio-label span {
  display: block;
  background: #fff;
  padding: 2px;
  border: 1px solid purple;
}

.horizontal-radios .radio-label:not(:first-child) span {
  border-left: none;
}

.horizontal-radios .radio-label input:checked+span {
  background-color: #beb6ce;
}

.list-radios {
  font-size: 12px;
}

.list-radios label {
  display: block;
}

.selected-tile-actions {
  display: none;
}

.selected-tile-actions.has-selection {
  display: block;
}

.creation-tile-type {
  display: none;
}

.creation-tile-type.enabled {
  display: block;
}
